<template>
  <div :class="$root.themeHomeChange === '1' ? 'overIncompatible_light' : 'overIncompatible'">
    <div class="overIncompatible-con">
      <div class="title">
        <bda-button @click="goBack">返回</bda-button>
        <h2>{{ infoData.taskName }}</h2>
      </div>
      <div class="information">
        <div class="information-left">
          <h4>基本信息</h4>
          <table
            class="table"
            v-loading="infoLoading"
            element-loading-text="拼命加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)">
            <tr>
              <td>任务名称：</td>
              <td>{{ infoData.taskName || '' }}</td>
              <td>发起日期：</td>
              <td>
                {{
                  infoData.timingTaskStartDate || ''
                }}
              </td>
            </tr>
            <tr>
              <td>任务编号：</td>
              <td>{{ infoData.projectCode || '' }}</td>
              <td>检查单位：</td>
              <td>{{ infoData.checkUnit || '' }}</td>
            </tr>
            <tr>
              <td>任务状态：</td>
              <td>{{ options.taskStatus[infoData.taskStatus] || '' }}</td>
              <td>被检查单位：</td>
              <td>{{ infoData.beCheckUnit || '' }}</td>
            </tr>
            <tr>
              <td>任务类型：</td>
              <td>{{ options.taskTypeOptions[infoData.taskType] || ''}}</td>
              <td>规则异常数：</td>
              <td style="color: #ffc800; font-size: 16px">
                {{ infoData.regularOutliers || '0' }}
              </td>
            </tr>
            <tr>
              <td>涉及金额：</td>
              <td>{{ infoData.amountInvolved || '0' }}</td>
              <td>检查时间段：</td>
              <td>{{( infoData.checkStartDate || '') + "~" + (infoData.checkEndDate || '') }}</td>
            </tr>

            <!-- <tr>
              <td>所属域：</td>
              <td colspan="3,4">财务管理/全业务管理</td>
            </tr> -->
            <tr>
              <td>任务说明：</td>
              <td colspan="3,4">
                {{ infoData.taskDesc || '' }}
              </td>
            </tr>
          </table>
        </div>
        <div class="information-center">
          <h4>风险监测情况</h4>
          <pie-chart
            v-loading="information_Loading"
            element-loading-text="拼命加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            :chartData="chartData"
          ></pie-chart>
        </div>
        <div class="information-right">
          <bar-chart
            :chartData="chartData"
          ></bar-chart>
        </div>
      </div>
      <div class="rulesList">
        <h2>规则列表</h2>
        <!--表单-->
        <el-form
          :model="form"
          :rules="rules"
          ref="form"
          label-width="84px"
          label-suffix="："
        >
          <el-row>
            <el-col :span="4">
              <bda-form-item label="规则分类" prop="ruleTypeList">
                <bda-select
                  v-model="form.ruleType"
                  size="small"
                  filterable
                  clearable
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <bda-option
                    v-for="item in ruleTypeList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name"
                  >
                  </bda-option>
                </bda-select>
              </bda-form-item>
            </el-col>
            <el-col :span="4">
              <bda-form-item label="归口部门" prop="department">
                <bda-select
                  v-model="form.department"
                  filterable
                  clearable
                  :searchable="false"
                  placeholder="请选择"
                  style="width: 100%"
                  size="small"
                >
                  <bda-option
                    v-for="item in departmentList"
                    :title="item.name"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name"
                  >
                  </bda-option>
                </bda-select>
              </bda-form-item>
            </el-col>
            <el-col :span="4">
              <bda-form-item label="风险等级" prop="level">
                <bda-select
                  v-model="form.level"
                  size="small"
                  filterable
                  clearable
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <bda-option
                    v-for="item in levelList"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name"
                  >
                  </bda-option>
                </bda-select>
              </bda-form-item>
            </el-col>
            <el-col :span="5">
              <bda-form-item label="检查时间">
                <bda-date-picker
                  v-model="form.checkDate"
                  :clearable="false"
                  range-separator="至"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                ></bda-date-picker>
              </bda-form-item>
            </el-col>
            <el-col :span="7">
              <bda-search-input
                v-model="keyValue"
                placeholder="请输入关键字"
                style="width: 50%; padding-left: 50%"
                @search="queryTaskTableByid(true)"
                @clear="queryTaskTableByid(true)"
              >
              </bda-search-input>
            </el-col>
          </el-row>
        </el-form>

        <div class="project-manage-table" ref="projectManageTableRef">
          <bda-scroll-table
            height="calc()"
            background
            :row-class-name="tableRowClassName"
            :data="tableData"
            ref="table"
            popper-append-to-body
            border
            :blInitLoad="false"
            tooltip-effect="light"
            v-loading="tableLoading"
            element-loading-text="拼命加载中"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
          >
            <el-table-column
              type="index"
              label="序号"
              min-width="60"
            ></el-table-column>
            <el-table-column
              prop="ruleName"
              label="规则名称"
              min-width="232"
            >
            </el-table-column>
            <el-table-column
              prop="ruleType"
              label="规则分类"
              min-width="100"
            ></el-table-column>
            <el-table-column
              prop="gkbm"
              label="归口部门"
              min-width="100"
            ></el-table-column>
            <el-table-column
              prop="ywlcjd"
              label="业务流程阶段"
              min-width="140"
            ></el-table-column>
            <el-table-column
              prop="ywsx"
              label="业务事项"
              min-width="168"
            ></el-table-column>
            <el-table-column
              prop="gzdy"
              label="规则定义"
              min-width="120"
              show-overflow-tooltip
            ></el-table-column>
            <el-table-column
              prop="fxdj"
              label="风险等级"
              min-width="100"
            ></el-table-column>
            <el-table-column
              prop="zrgw"
              label="责任岗位"
              min-width="120"
            ></el-table-column>
            <el-table-column
              prop="gzslm"
              label="规则实例名"
              min-width="260"
            ></el-table-column>
            <el-table-column
              prop="wjtwsy"
              label="文件条文索引"
              min-width="300"
            ></el-table-column>
            <el-table-column prop="jcycqk" label="监测异常情况" min-width="120">
              <template slot-scope="scope">
                <span
                  style="color: #ff5a5a; font-weight: bold;"
                  v-if="scope.row.jcycqk == 1"
                >异常</span>
                <span v-else sstyle="color: #33d886;">{{ scope.row.jcycqk == 0 ? "正常" : "监测中" }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="jcycqk" label="异常数" min-width="120">
              <template slot-scope="scope">
                <span
                  title="点击查看异常详情"
                  style="cursor: pointer; color: #ff5a5a; font-weight: bold;"
                  v-if="scope.row.ycs > 0"
                  @click="detailDialogClick(scope.row)"
                >{{ scope.row.ycs }}</span>
                <span v-else sstyle="color: #33d886;" >{{ scope.row.ycs }}</span>
              </template>
            </el-table-column>
            <el-table-column
              prop="monitorTime"
              label="监测时间"
              min-width="160"
            >
              <template slot-scope="scope">
                {{ formatDate(scope.row.monitorTime, 'YYYY-MM-DD HH:mm:ss') }}
              </template>
            </el-table-column>
          </bda-scroll-table>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pageInfoConfig.page"
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="pageInfoConfig.rows"
            background
            layout="prev, pager, next, sizes, jumper, total"
            :total="pageInfoConfig.total"
          >
          </el-pagination>
        </div>
      </div>


      <detail-dialog ref="detailDialogRef" :tableData="detailInfo"></detail-dialog>
    </div>
  </div>
</template>

<script>
import pieChart from "@/views/components/riskControlCenter/pie-charts";
import barChart from "@/views/components/riskControlCenter/bar-charts.vue";
import detailDialog from "../component/dialog/detailDialog";
import Interface from "@/base/mixin/risk-control-center.js";
import qs from "qs";
import moment from "moment";

export default {
  components: { pieChart, barChart, detailDialog},
  mixins: [Interface],
  data() {
    return {
      //form
      form: {
        ruleType: "", //规则分类
        department: "", //归口部门
        level: "", //风险等级
        checkDate: [],
      },
      
      detailInfo: [], // 详细信息
      
      keyValue: '', //关键字
      
      chartData: {}, // 图表数据
      
      infoLoading: false,  // 基本信息加载
      
      infoData: {}, // 基本信息
      
      // 表格加载
      tableLoading: false,
      
      // 风险监测情况加载
      information_Loading: false,
      
      /*表格数据 */
      tableData: [],

      pageInfoConfig: {
        page: 1,
        rows: 20,
        total: 0,
      }, //表格-分页初始配置
      
      options: {
        taskTypeOptions: {
          '1': '实时性检查',
          '2': '计划性检查',
          '3': '专项性检查'
        },
        taskStatus: {
          '1': '已创建',
          '2': '进行中',
          '3': '已完成',
          '4': '已停用'
        },
        taskPerformSync: [
          { id: 1, value: 'one-day', name: "每天" },
          { id: 2, value: 'three-day', name: "每三天" },
          { id: 3, value: 'a-week', name: "每一周" },
          { id: 4, value: 'half-a-month', name: "每半个月" },
          { id: 5, value: 'a-month', name: "每一个月" },
          { id: 6, value: 'three-month', name: "每三个月" },
          { id: 7, value: 'half-a-year', name: "每半年" },
          { id: 8, value: 'a-month', name: "每一年" },
        ],
      },
      
      rules: {},
      departmentList: [], //部门列表
      ruleTypeList: [], //规则分类列表
      levelList: [
        { id: 1, name: "轻微" },
        { id: 2, name: "一般" },
        { id: 3, name: "较大" },
        { id: 4, name: "重大" },
      ], //风险等级列表
    };
  },
  created() {
    // 获取基本信息
    this.initTableInfoByid(this.$route.query.taskId);
    // 获取规则列表
    this.queryTaskTableByid();
    // 获取缺陷图表数据
    this.getChartDataByid(this.$route.query.taskId);
    // 初始化规则列表参数
    this.initTableParamsByTaskId(this.$route.query.taskId);
  },
  mounted() {},
  watch: {
    form: {
      handler(v) {
        this.queryTaskTableByid(true, v);
      },
      immediate: false,
      deep: true
    },
  },
  methods: {
    // 初始化合规任务表格，通过taskId
    initTableInfoByid(taskId) {
      // window.console.log(taskId)
      this.infoLoading = true;
      this.getRiskTaskInfo(qs.stringify({taskId})).then((rs)=>{
        this.infoData = rs;
      }).catch(err => {
        this.$message.error(err.code);
        this.infoData = {};
      }).finally(()=>{
        this.infoLoading = false;
      })
    },
    
    // 初始化规则列表参数
    initTableParamsByTaskId(id) {
      // 获取规则分类
      this.selectRuleTypeByTaskId({ taskId: id }).then((rs)=>{
        window.console.log(rs)
        this.ruleTypeList = rs.map((item, index) => { return { id: index + item, name: item }; });
      }).catch(err => {
        this.ruleTypeList = [];
        this.$message.error(err.code);
      }).finally(()=>{})
      
      // 获取归口部门
      this.selectDeptByTaskId({ taskId: id }).then((rs)=>{
        // window.console.log(rs)
        this.departmentList = rs.map((item, index) => { return { id: index + item, name: item }; });
      }).catch(err => {
        this.departmentList = [];
        this.$message.error(err.code);
      }).finally(()=>{})
    },
    
    // 初始化合规任务表格，通过taskId
    getChartDataByid(taskId) {
      // window.console.log(taskId)
      this.information_Loading = true;
      this.getRuleDetection(qs.stringify({taskId})).then((rs)=>{
        this.chartData = rs;
        if(rs) {
          this.chartData = rs;
          // this.chartData = {
          //   "level1Number": 2,
          //   "level2Number": 3,
          //   "level3Number": 5,
          //   "level4Number": 12,
          //   "list": [
          //     {
          //       deptName: '测试1',
          //       level1Number: 1,
          //       level2Number: 3,
          //       level3Number: 5,
          //       level4Number: 12,
          //       total: 21
          //     },
          //     {
          //       deptName: '测试2',
          //       level1Number: 1,
          //       level2Number: 3,
          //       level3Number: 5,
          //       level4Number: 12,
          //       total: 21
          //     },
          //     {
          //       deptName: '测试1',
          //       level1Number: 1,
          //       level2Number: 3,
          //       level3Number: 5,
          //       level4Number: 12,
          //       total: 21
          //     },
          //     {
          //       deptName: '测试2',
          //       level1Number: 1,
          //       level2Number: 3,
          //       level3Number: 5,
          //       level4Number: 12,
          //       total: 21
          //     },
          //     {
          //       deptName: '测试1',
          //       level1Number: 1,
          //       level2Number: 3,
          //       level3Number: 5,
          //       level4Number: 12,
          //       total: 21
          //     },
          //     {
          //       deptName: '测试2',
          //       level1Number: 1,
          //       level2Number: 3,
          //       level3Number: 5,
          //       level4Number: 12,
          //       total: 21
          //     }
          //   ]
          // };
        } else {
          this.chartData = {};
        }
      }).catch(err => {
        this.$message.error(err.code);
      }).finally(()=>{
        this.information_Loading = false;
      })
    },
    
    // 格式化数据
    formatDate(time, type) {
      return time ? moment(time).format(type) : ' / ';
    },
    
    queryTaskTableByid(resetPage) {
      if(resetPage) {
        this.resetPage();
      }
      let data = {
        current: this.pageInfoConfig.page,
        department: this.form.department,
        end: this.form.checkDate[1] || '',
        level: this.form.level,
        ruleType: this.form.ruleType,
        size: this.pageInfoConfig.rows,
        start: this.form.checkDate[0] || '',
        taskId: this.$route.query.taskId,
        key: this.keyValue
      }
      this.tableLoading = true;
      this.getRuleList(data).then((rs)=>{
        // window.console.log(rs)
        if (Array.isArray(rs.list)) {
          this.tableData = rs.list;
        } else {
          this.tableData = [];
        }
        this.pageInfoConfig.total = rs.total || 0;
      }).catch(err => {
        this.$message.error(err.code);
        this.tableData = [];
        this.resetPage();
      }).finally(()=>{
        this.tableLoading = false;
      })
    },
    
    // 重置分页参数
    resetPage(page = 1, rows = 20, total = 0) {
      this.pageInfoConfig = {
        page,
        rows,
        total
      };
    },
    
    tableRowClassName({ row }) {
      if (row.jcycqk == 1) {
        return "warning-row";
      } else if (row.jcycqk) {
        return "success-row";
      }
      return "";
    },

    goBack() {
      this.$router.push({
        path: "/overManage",
        query: {
          index: "3-2",
          actNav: this.$route.query.actNav,
        },
      });
    },
    addDialogClick() {
      this.$refs.addDialogRef.openDialog();
    },

    handleSizeChange(val) {
      // window.console.log(`每页 ${val} 条`);
      this.pageInfoConfig.rows = val;
      this.queryTaskTableByid(false);
    },
    handleCurrentChange(val) {
      // window.console.log(`当前页: ${val}`);
      this.pageInfoConfig.page = val;
      this.queryTaskTableByid(false);
    },
    
    // 查看异常详情
    detailDialogClick(row){
      this.selectMonitorAbnormalTaskId({id: row.id}).then((rs)=>{
        // window.console.log('异常详情', rs);
        if (Array.isArray(rs)) {
          this.detailInfo = rs;
        } else {
          this.detailInfo = [];
        }
      }).catch((err) => {
        this.detailInfo = [];
        this.$message.error('异常详情报错： ' + err.code);
      }).finally(()=>{
        this.$refs.detailDialogRef.openDialog()
      })
    }
  },
};
</script>

<style lang="scss" scoped>
.overIncompatible {
  /deep/ {
    width: 100%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;

    .overIncompatible-con {
      width: 100%;
      height: 100%;

      .title {
        width: 100%;
        height: 42px;
        position: relative;
        border-bottom: 1px solid #0065b3;

        > h2 {
          font-family: MicrosoftYaHei-Bold;
          font-size: 20px;
          line-height: 42px;
          color: #ffffff;
          text-align: center;
          font-weight: 700;
          margin: 0;
        }

        .el-button {
          background-color: #041338;
          border: 1px solid #4389f9;
          border-radius: 4px;
          color: #fff;
          position: absolute;
          left: 0;
          top: 6px;
        }
      }

      // 基本信息
      .information {
        width: 100%;
        height: 341px;
        padding-bottom: 20px;
        display: flex;

        // 左侧表格样式
        .information-left {
          width: 44%;

          > h4 {
            margin: 20px 0;
            font-family: MicrosoftYaHei-Bold;
            font-size: 16px;
            line-height: 16px;
            color: #ffffff;
            font-weight: 700;
          }

          .table {
            width: 100%;
            height: calc(100% - 56px);
            box-sizing: border-box;

            > tr {
              > td {
                box-sizing: border-box;
                padding-left: 10px;
                font-family: MicrosoftYaHei-Bold;
                font-size: 14px;
                color: #ffffff;
                font-weight: 700;
              }

              > td:nth-of-type(odd) {
                width: 120px;
                color: #9fb6dc;
                background: #1e4178;
                text-align: right;
              }
              > td:nth-of-type(even) {
                width: 280px;
                background: #1a3066;
              }
            }
          }
        }

        // 中间样式
        .information-center {
          flex: 1;

          > h4 {
            margin: 0;
            padding: 20px 0 20px 60px;
            font-family: MicrosoftYaHei-Bold;
            font-size: 16px;
            line-height: 16px;
            color: #ffffff !important;
            font-weight: 700;
          }
        }

        // 右侧样式
        .information-right {
          width: 27.5%;
        }
      }

      // 规则列表
      .rulesList {
        width: 100%;
        height: calc(100% - 42px - 361px);

        > h2 {
          height: 36px;
          margin: 0;
          font-family: MicrosoftYaHei-Bold;
          font-size: 16px;
          color: #ffffff;
          font-weight: 700;
        }

        .el-button {
          background-color: #041338;
          border: 1px solid #4389f9;
          border-radius: 4px;
          color: #fff;
        }
        .el-select__caret {
          color: #4298f3;
        }

        .el-form-item__label {
          color: #fff;
        }

        // 条件筛选表单样式
        .el-form {
          margin-bottom: 10px;
          .el-row {
            height: 32px;
            .el-col {
              height: 32px;

              .el-input__inner {
                background: #041338;
                border: 1px solid #4389f9;
                border-radius: 4px;
                color: #4298f3;
              }
              .el-input__inner::placeholder {
                color: #4298f3 !important;
              }

              .el-date-editor {
                width: 100%;

                .el-range-input {
                  background-color: #041338;
                  color: #4298f3;
                  &::placeholder {
                    color: #4298f3;
                  }
                }
                .el-range-separator {
                  color: #fff;
                }

                .el-input__icon,
                .el-input__suffix {
                  background: #041338 !important;
                }
              }
            }
          }
        }

        // 表格样式
        .project-manage-table {
          height: calc(100% - 159px);
          width: 100%;
          .button-container {
            margin-bottom: 10px;
          }

          /*修改表格单元格高度*/
          .bda-pagination-table {
            .bda-pagination-table-page {
              border: 0 !important;
              height: 50px !important;
            }
          }

          /*checkbox样式*/

          .el-checkbox__input.is-disabled {
            width: 100%;
            padding: 0 auto;
          }

          .el-checkbox__input.is-disabled .el-checkbox__inner,
          .el-checkbox__input .el-checkbox__inner {
            background-color: #041338 !important;
            width: 16px;
            height: 16px;
            border: 1px solid #4389f9;
            border-radius: 2px;
          }

          /*解决调整单元格高度后，fixed错位*/
          .el-table__fixed-body-wrapper {
            top: 37px !important;
          }

          /*解决表格滚动高度固定导致的问题*/
          .el-table__body-wrapper.is-scrolling-left {
            height: calc(100% - 48px) !important;
          }
          .el-table__body-wrapper.is-scrolling-right {
