<template>
  <div :class="$root.themeHomeChange === '1' ? 'pieCharts_light' : 'pieCharts'">
    <div class="titles">
      <div>单位名称</div>
      <div>缺陷占比情况</div>
      <div>
        缺陷总数
      </div>
    </div>
    <div id="pieCharts" ref="pieCharts"></div>
    <div v-if="!(Array.isArray(chartData.list) && chartData.list.length !== 0)" class="tips">暂无数据</div>
  </div>
</template>

<script>
import * as echarts from "echarts";
// import { color } from "highcharts";
//这是绘制图表的参数定义
let optionTemp = {
  tooltip: {
    trigger: "axis",
    axisPointer: {
      // Use axis to trigger tooltip
      type: "shadow", // 'shadow' as default; can also be 'line' or 'shadow'
    },
  },
  legend: {
    show: false,
    top: "20",
  },
  grid: {
    left: "10%",
    right: "4%",
    bottom: "3%",
    containLabel: true,
  },
  xAxis: {
    show: false,
    type: "value",
  },
  // dataZoom: [
  //   //滑动条
  //   {
  //     yAxisIndex: 0, //这里是从X轴的0刻度开始
  //     show: true, //是否显示滑动条，不影响使用
  //     type: "slider", // 这个 dataZoom 组件是 slider 型 dataZoom 组件
  //     startValue: 0, // 从头开始。
  //     endValue: 3, // 一次性展示6个。
  //   },
  // ],

  yAxis: [
    {
      axisTick: [
        {
          //坐标轴小标记
          show: false,
        },
      ],
      axisLine: { show: false }, //坐标轴
      axisLabel: {
        //设置y轴的字
        show: true,
        // formatter: '{value} ml',
        color: "#fff",
        interval: 0, //使y轴横坐标全部显示
        textStyle: {
          //y轴字体样式
          color: "#fff",
          margin: 15,
        },
      },
      type: "category",
      data: [],
    },
    {
      axisTick: [
        {
          //坐标轴小标记
          show: false,
        },
      ],
      axisLine: { show: false }, //坐标轴
      axisLabel: {
        //设置y轴的字
        show: true,
        // formatter: '{value} ml',
        color: "#ffc800",
        interval: 0, //使y轴横坐标全部显示
        textStyle: {
          //y轴字体样式
          color: "#ffc800",
          fontWeight: 'bold',
          fontSize: 14,
          margin: 15,
        },
      },
      type: "category",
      data: [],
    }
  ],
  series: [
    {
      name: "轻微缺陷",
      type: "bar",
      barWidth: 12, //柱宽度
      stack: "total",
      itemStyle: {
        normal: {
          label: {
            show: true,
            position: "top",
            formatter: function (params) {
              // window.console.log(params);
              if (params.value === 0) {
                return "";
              } else {
                return params.value;
              }
            },
            textStyle: {
              color: "#08a4fa",
            },
          },
          color: "#0490EC",
        },
      },
      label: {
        show: true,
      },
      emphasis: {
        focus: "series",
      },
      data: [],
    },
    {
      name: "一般缺陷",
      type: "bar",
      barWidth: 12, //柱宽度
      stack: "total",
      itemStyle: {
        normal: {
          label: {
            show: true,
            position: "top",
            formatter: function (params) {
              // window.console.log(params);
              if (params.value === 0) {
                return "";
              } else {
                return params.value;
              }
            },
            textStyle: {
              color: "#FFC800",
            },
          },
          color: "#19CFDF",
        },
      },
      label: {
        show: true,
      },
      emphasis: {
        focus: "series",
      },
      data: [],
    },
    {
      name: "较大缺陷",
      type: "bar",
      barWidth: 12, //柱宽度
      stack: "total",
      itemStyle: {
        normal: {
          label: {
            show: true,
            position: "top",
            formatter: function (params) {
              // window.console.log(params);
              if (params.value === 0) {
                return "";
              } else {
                return params.value;
              }
            },
            textStyle: {
              color: "#FFC800",
            },
          },
          color: "#EDC54E",
        },
      },
      label: {
        show: true,
      },
      emphasis: {
        focus: "series",
      },
      data: [],
    },
    {
      name: "重大缺陷",
      type: "bar",
      barWidth: 12, //柱宽度
      stack: "total",
      itemStyle: {
        normal: {
          label: {
            show: true,
            position: "top",
            formatter: function (params) {
              // window.console.log(params);
              if (params.value === 0) {
                return "";
              } else {
                return params.value;
              }
            },
            textStyle: {
              color: "#FFC800",
            },
          },
          color: "#FF7556",
        },
      },
      label: {
        show: true,
      },
      emphasis: {
        focus: "series",
      },
      data: [],
    },
  ],
};

export default {
  name: "pieCharts",
  props: {
    index: {
      type: Number,
      default: () => {
        return null;
      },
    },
    xData: {
      /*** * xData:['一月',二月]*/
      type: Array,
      default: () => {
        return [];
      },
    },
    seriesData: {
      type: Array,
      default: () => {
        return [];
      },
    },
    //  决定x轴和y轴显示(单位)
    _zb: {
      /** :['单位','']*/
      type: Array,
      default: () => {
        return ["单位", ""];
      },
    },
    //  图表数据
    chartData: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  watch: {
    chartData: {
      handler(v) {
        if(Array.isArray(v.list) && v.list.length !== 0) {
          this.init();
        }
      },
      immediate: false,
      deep: true
    },
    '$root.themeHomeChange'() {
      this.init();
    }
  },
  data() {
    return {
      option: optionTemp,
    };
  },
  mounted() {
    if(Array.isArray(this.chartData.list) && this.chartData.list.length !== 0) {
      this.init();
    }
  },
  methods: {
    setTheme(){
      // window.console.log(23123);
      //样式区分
      if (this.$root.themeHomeChange === '1'){  //深色背景
        this.option.yAxis[0].axisLabel.textStyle.color = "#333";
      }else if(this.$root.themeHomeChange==='2'){ //浅色背景
        this.option.yAxis[0].axisLabel.textStyle.color = "#fff";
      }
    },
    dataSet() {
      //   this.option.yAxis[0].name = this._zb[0];
      //   this.option.xAxis[0].name = this._zb[1];
      //   this.option.xAxis[0].data = this.xData;
      //   if (this.seriesData.length > 0) {
      //     this.option.legend.data = [];
      //     this.seriesData.forEach((item) => {
      //       this.option.legend.data.push(item.name);
      //     });
      //     this.option.series = this.seriesData;
      //   }
      //   this.option.series.forEach(el=>{
      //       console.log(this);
      //       el.itemStyle = this.itemStyle
      //   })
      this.option.yAxis[0].data = Array.isArray(this.chartData.list) ? this.chartData.list.map(item => item.deptName) : [];
      this.option.yAxis[1].data = Array.isArray(this.chartData.list) ? this.chartData.list.map(item => item.total) : [];
      this.option.series.forEach((item, index) => {
        item.data = Array.isArray(this.chartData.list) ? 
        this.chartData.list.map(item => item['level'+ (4 - index) + 'Number']) : [];
      });
      this.setTheme();
    },
    init() {
      this.dataSet()
      let mychart = echarts.init(this.$refs.pieCharts);
      window.addEventListener("resize", mychart.resize);
      //将会绘制图表的参数加到图表中

      mychart.setOption(this.option);
      let $this = this;
      mychart.on("click", function (params) {
        $this.$emit("getData", params, $this.index);
      });
    },
  },
};
</script>

<style scoped lang="scss">
.pieCharts {
  height: 100%;
  position: relative;

  .tips {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    font-size: 12px;
    font-weight: bold;
    color: #fff;
  }
  .titles {
    position: absolute;
    width: 100%;
    height: calc(100% - 36px);
    margin-top: 36px;
    color: #fff;
    display: flex;
    > div {
      font-size: 12px;
      line-height: 12px;
      color: #ffffff;
      text-align: right;
      font-weight: 400;
    }

    > div:nth-of-type(1) {
      width: 25%;
      text-align: center;
    }
    > div:nth-of-type(2) {
      flex: 1;
      width: calc(75% - 48px);
      text-align: center;
    }
    > div:nth-of-type(3) {
      width: 48px;
      > p {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        height: calc(100% - 15px);
        margin-top: 15px;
        > span {
          height: 122px; //2组宽度
          line-height: 122px; //4组宽度
          // height: 62px; //4组宽度
          // line-height: 60px;//4组宽度
          font-size: 14px;
          color: #ffc800;
          text-align: center;
          font-weight: 700;
        }
      }
    }
  }

  #pieCharts {
    width: 100%;
    height: calc(100% - 45px);
    top: 50px;
  }
}

// 浅色主题
.pieCharts_light {
  height: 100%;
  position: relative;

  .tips {
    width: 100%;
    text-align: center;
    position: absolute;
    top: 50%;
    font-size: 12px;
    font-weight: bold;
    color: #333;
  }
  .titles {
    position: absolute;
    width: 100%;
    height: calc(100% - 36px);
    margin-top: 36px;
    color: #333;
    display: flex;
    > div {
      font-size: 12px;
      line-height: 12px;
      color: #333;
      text-align: right;
      font-weight: 400;
    }

    > div:nth-of-type(1) {
      width: 25%;
      text-align: center;
    }
    > div:nth-of-type(2) {
      flex: 1;
      width: calc(75% - 48px);
      text-align: center;
    }
    > div:nth-of-type(3) {
      width: 48px;
      > p {
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        height: calc(100% - 15px);
        margin-top: 15px;
        > span {
          height: 122px; //2组宽度
          line-height: 122px; //4组宽度
          // height: 62px; //4组宽度
          // line-height: 60px;//4组宽度
          font-size: 14px;
          color: #ffc800;
          text-align: center;
          font-weight: 700;
        }
      }
    }
  }

  #pieCharts {
    width: 100%;
    height: calc(100% - 45px);
    top: 50px;
  }
}
</style>