<template>
  <bda-dialog
    :class="$root.themeHomeChange === '1' ? 'detailDialog_light' : 'detailDialog'"
    :visible="dialogVisible"
    :modal="false"
    :close-on-click-modal="false"
    @close="closeDialog"
    @cancel="closeDialog"
    title="异常明细"
  >
    <div class="dialogBox">
      <el-table
        :data="tableData"
        tooltip-effect="light"
        border
        style="width: 100%">
        <el-table-column type="index" label="序号" width="60"></el-table-column>
        <!-- <el-table-column
          prop="dwmc"
          label="单位名称"
          min-width="120"
          show-overflow-tooltip
        ></el-table-column> -->
        <el-table-column
          prop="dxbh"
          label="对象编号"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="dxmc"
          label="对象名称"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <!-- <el-table-column
          prop="gdbh"
          label="工单编号"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="bzz"
          label="标准值"
          min-width="80"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="sjz"
          label="实际值"
          min-width="80"
          show-overflow-tooltip
        ></el-table-column> -->
        <el-table-column
          prop="gds"
          label="供电分局"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="yddz"
          label="用电地址"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="tq"
          label="台区"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="xmbh"
          label="项目编号"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="xmmc"
          label="项目名称"
          min-width="100"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="updateTime"
          label="更新时间"
          show-overflow-tooltip
          min-width="120"
        ></el-table-column>
        <!-- <el-table-column prop="num" label="异常数"> </el-table-column> -->
        <!-- <el-table-column
          prop="defectDesc"
          label="异常描述"
          show-overflow-tooltip
          min-width="150"
        ></el-table-column> -->
        <!-- <el-table-column prop="num" label="异常数"> </el-table-column> -->
      </el-table>
    </div>
    <span slot="footer" class="dialog-footer">
      <bda-button bdatype="defualt" @click="closeDialog">关闭</bda-button>
    </span>
  </bda-dialog>
</template>

<script>
export default {
  props: {
    tableData: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      dialogVisible: false, //弹框显隐
      // tableData: [
      //   {
      //     id: 'XXX',
      //     name: 'XXX',
      //     unit: "古镇供电分局计量运维班",
      //     abnormalNum: "1",
      //     describe: '古镇供电分局计量运维班异常描述'
      //   },
      //   {
      //     id: 'XXX',
      //     name: 'XXX',
      //     unit: "古镇供电分局配电运维班",
      //     abnormalNum: "1",
      //     describe: '古镇供电分局配电运维班异常描述'
      //   },
      // ],
    };
  },

  methods: {
    /**
     * @description:打开弹框回调
     */
    openDialog() {
      this.dialogVisible = true;
    },

    /**
     * @description:关闭弹框回调
     */
    closeDialog() {
      this.dialogVisible = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.detailDialog {
  /deep/ {
    /*修改添加规则弹窗 */
    .el-dialog {
      margin-top:45vh !important;
      width: 1220px;
      background: #1f2c4d;
      border: 1px solid #4389f9;
      border-radius: 4px;
  
      .el-dialog__header {
        border-bottom: 1px solid #4389f9 !important;
        height: 48px !important;
  
        .bda-dialog-header .bda-dialog-header-title {
          font-family: MicrosoftYaHei-Bold;
          font-size: 16px !important;
          color: #ffffff !important;
          line-height: 48px;
          font-weight: 700;
        }
      }
  
      .bda-dialog-body-main {
        .el-table {
          margin-left: -1px;
          border-color: #000;
          &::before,
          &::after {
            background-color: #000;
          }
  
          .el-table__header-wrapper {
            > tr,
            th {
              background-color: #22519c !important;
              border-color: #000;
              .cell {
                text-align: center;
                line-height: 48px;
                height: 48px;
                color: #fff;
              }
            }
          }
          .el-table__body-wrapper {
            background-color: #223a75;
            .el-table__row:nth-of-type(odd) {
              background-color: #223a75;
            }
            .el-table__row:nth-of-type(even) {
              background-color: #1a3066;
            }
            .el-table__row {
              border-color: #000;
  
              &:hover > td {
                background-color: #284385 !important;
              }
  
              > td {
                border-color: #000;
                .cell {
                  text-align: center;
                  line-height: 48px;
                  height: 48px;
                  color: #fff;
                }
              }
            }
          }
        }
      }
  
      .el-dialog__footer {
        border-top: 1px solid #4389f9 !important;
        height: 52px !important;
  
        .dialog-footer {
          display: block;
          height: 100%;
          padding: 11px 20px;
          box-sizing: border-box;
  
          .bda-button {
            background-color: #041338;
            border: 1px solid #4389f9;
            border-radius: 4px;
            color: #fff;
          }
        }
      }
    }
  }
}

// 浅色主题
