              &:hover > td {
                background-color: rgba(187, 187, 187, 0.2)!important;
              }
  
              > td {
                border-color: #bbb;
                .cell {
                  text-align: center;
                  line-height: 48px;
                  height: 48px;
                  color: #333;
                }
              }
            }
          }
        }
      }
  
      .el-dialog__footer {
        border-top: 1px solid #bbb !important;
        height: 52px !important;
  
        .dialog-footer {
          display: block;
          height: 100%;
          padding: 11px 20px;
          box-sizing: border-box;
  
          .bda-button {
            background-color: #fff;
            border: 1px solid #4389f9;
            border-radius: 4px;
